body {
    margin: 0;
    background: linear-gradient(220.55deg, #4643df 0%, #0b0a47 100%);
    min-height: 100vh;
    font-family: Segoe UI, Tahoma, sans-serif;
}

.container {
    padding: 0;
    padding-bottom: 0;
    margin-bottom: 0;
}

.pageNumber {
    padding: 0;
    margin-top: 0;
}

.pageBtn {
    width: 44px;
    height: 44px;
    background: white;
    border: 0;
    font: inherit;
    font-size: 0.8em;
}
