.section {
    position: relative;
    width: 100%;
    height: 89vh;
}

container {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.pdfViewer {
    top: 0;
    left: 0;
    bottom: 0;
    right: 50%;
    overflow: hidden;
}

.summaryBlocks {
    position: absolute;
    top: 0;
    left: 50%;
    right: 0;
    overflow: scroll;
    bottom: 0;
    padding-bottom: 2%;
    overflow-x: hidden;
}

.showPdf {
    display: block;
}

.hidePdf {
    display: none;
}
