.container {
    margin: 5rem auto;
    color: white;
    display: grid;
    background-color: #a3c3e7;
    padding: 3rem;
    max-width: 60rem;
    border-radius: 3rem;
    box-shadow: 1px 1px 3px 2px #a3c3e7;
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
        "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

.grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    background: linear-gradient(#fff, #fff) center/1px 100% no-repeat;
    border-bottom: 1px solid white;
}

.griditem {
    display: flex;
    flex-direction: column;
    margin-left: 1rem;
    justify-content: center;
    min-height: 3rem;
    margin-bottom: 3%;
    margin-top: 8%;
}

.rborder {
    font-size: 1.75rem;
    margin-bottom: 0;
    min-width: 80%;
    border-radius: 5px;
}

.submitBtn {
    background-color: #8bb9ee;
    min-width: 80%;
    min-height: 3rem;
    border-radius: 7px;
    margin-top: 3%;
    margin-left: 10%;
    font-size: 1.2rem;
    font-weight: 800;
    color: rgb(24, 41, 41);
}
